@keyframes fadeInUp { from { opacity: 0; transform: translateY(20px); } to { opacity: 1; transform: translateY(0); } }
@keyframes pulse { 0% { transform: scale(1); } 50% { transform: scale(1.05); } 100% { transform: scale(1); } }
@keyframes flicker { 0%,100% { opacity: 0.95; transform: translateY(0) scale(1); } 50% { opacity: 0.6; transform: translateY(-2px) scale(0.95); } }
@keyframes spin { to { transform: rotate(360deg); } }

.h5-root { min-height: calc(100vh - 56px); display: flex; align-items: center; justify-content: center; padding: 20px; }
.card { width: 100%; max-width: 600px; border-radius: 16px; box-shadow: 0 12px 32px rgba(0,0,0,0.12); background: rgba(255,255,255,0.9); backdrop-filter: blur(6px); padding: 24px; text-align: center; animation: fadeInUp .4s ease both; }
.title { font-size: 1.8rem; margin-bottom: 8px; }
.subtitle { font-size: 1rem; opacity: .85; }
.actions { margin-top: 16px; display: flex; justify-content: center; gap: 12px; }
.btn { padding: 10px 16px; border-radius: 10px; text-decoration: none; background: #ecf0f1; color: #2c3e50; display: inline-block; }
.btn.primary { background: #3498db; color: #fff; }
.btn.ghost { background: transparent; border: 1px dashed #bbb; }

/* 1. 启动页：时光信封 */
.envelope { position: relative; width: 280px; height: 180px; margin: 0 auto 18px; perspective: 800px; }
.env-body { position: absolute; inset: 0; border-radius: 12px; background: #c8a580; box-shadow: 0 8px 20px rgba(0,0,0,0.15); }
.env-flap { position: absolute; left: 0; right: 0; top: 0; height: 60%; background: linear-gradient(180deg,#b9926a,#c8a580); transform-origin: top; border-radius: 12px 12px 0 0; transition: transform .8s ease; }
.env-open .env-flap { transform: rotateX(160deg); }
.env-label { position: absolute; inset: 0; display: flex; align-items: center; justify-content: center; color: #3d2b1f; font-weight: 700; text-align: center; padding: 10px; }
.smiles { position: absolute; bottom: -12px; left: 50%; transform: translateX(-50%); display: flex; gap: 8px; }
.smile { width: 14px; height: 14px; background: #ffd54f; border-radius: 50%; box-shadow: 0 2px 6px rgba(0,0,0,0.2); animation: pulse 1.6s infinite; }

/* 2. 时光回溯 */
.timeline-wrap { margin: 12px 0; overflow: hidden; }
.nodes { display: flex; position: relative; }
.node { flex: 0 0 100%; padding: 12px; }
.node-card { border-radius: 12px; background: #fff7e6; padding: 16px; box-shadow: 0 6px 18px rgba(0,0,0,0.08); }
.node-icons { font-size: 22px; margin: 8px 0; }
.node-photo { margin-top: 8px; height: 280px; border-radius: 12px; background: #ffffff; display: flex; align-items: center; justify-content: center; color: #8d6e63; border: 1px solid #eee; overflow: hidden; }
.node-photo img { width: 100%; height: 100%; object-fit: contain; border-radius: 12px; box-shadow: 0 2px 8px rgba(0,0,0,0.08); }

@media (max-width: 768px) {
  .node-photo { height: 320px; }
}
.pager { display: flex; justify-content: space-between; margin-top: 10px; }

/* 3. 吐槽告白 */
.typing { min-height: 140px; font-family: monospace; text-align: left; border: 1px dashed #bbb; padding: 12px; border-radius: 10px; background: #f8fbff; }
.floating-zone { position: relative; height: 160px; margin-top: 10px; overflow: hidden; }
.icon { position: absolute; font-size: 24px; animation: fadeInUp .6s ease both; }
.heart { color: #e74c3c; }

/* 4. 蛋糕与蜡烛 */
.cake { position: relative; margin: 0 auto; width: 320px; height: 180px; background: #ffe0e0; border-radius: 12px; box-shadow: inset 0 -6px 0 #ffb3b3; overflow: hidden; }
.candles { position: absolute; top: 14px; left: 10px; right: 10px; display: grid; grid-template-columns: repeat(12, minmax(0, 1fr)); column-gap: 6px; row-gap: 8px; }
.candle { width: 12px; height: 26px; background: #f39c12; border-radius: 3px; position: relative; }
.flame { position: absolute; top: -8px; left: 50%; transform: translateX(-50%); width: 7px; height: 9px; background: radial-gradient(circle at 50% 30%, #ffeaa7 0%, #fdcb6e 60%, transparent 70%); border-radius: 50% 50% 30% 30%; animation: flicker 1.2s infinite ease-in-out; }
.candle.off .flame { display: none; }
.blow-hint { margin-top: 10px; font-size: .95rem; color: #555; }

/* 5. 契约书 */
.contract { padding: 10px; background: #fff; border-radius: 12px; box-shadow: 0 8px 20px rgba(0,0,0,0.08); }
.contract h3 { margin: 0 0 8px 0; }
.contract input { width: 100%; padding: 10px; border-radius: 8px; border: 1px solid #ddd; }
.stamp { display: inline-block; margin-top: 12px; padding: 10px 16px; border-radius: 50%; background: rgba(255,0,0,0.1); color: #c0392b; font-weight: 700; border: 2px solid #c0392b; animation: pulse 1.2s ease infinite; }
.sky { margin-top: 12px; height: 140px; border-radius: 12px; background: radial-gradient(circle at 50% 30%, #fff 0%, rgba(255,255,255,0.6) 8%, transparent 30%), linear-gradient(180deg,#1b2735,#090a0f); color: #fff; display: flex; align-items: center; justify-content: center; letter-spacing: 2px; }

/* 6. 结尾页 */
.final-photo { height: 220px; border-radius: 12px; background: radial-gradient(circle, #fff 0%, #eee 35%, #ddd 70%); display: flex; align-items: center; justify-content: center; font-size: 18px; color: #666; }
.final-photo img { width: 100%; height: 100%; object-fit: contain; border-radius: 12px; box-shadow: 0 2px 8px rgba(0,0,0,0.08); }
.gift-hint { margin-top: 10px; padding: 10px; background: #fffbe6; border-radius: 10px; border: 1px dashed #f1c40f; }
.share { display: inline-flex; align-items: center; gap: 6px; margin-left: 10px; }
.share .btn { animation: spin 1.4s linear infinite; }
